<template>
<div id="content">
  <router-link to="/assess_list" class="link-back"><img :src="iconreback"></router-link>
      <!-- 进度条 -->
      <progressBar greenwidth='20%'></progressBar>
      <!--标题-->
      <br />
      <div id="peerName" style="margin:10px auto;font-size: 18px;font-weight: bold;text-align: center;">{{hwInfo.name}}</div>

      <div id="homework-peerAssess">

        <div class="peerRequest">
          <span>作业要求：</span><span id="peerReq">{{hwInfo.requirement}}</span>
        </div>

        <div class="peerRound">
          <span>评估轮次：</span><span id="peerround">{{hwInfo.round}}</span>
        </div>

        <div class="peerExtendFrom">
          <span>源自：</span><span id="peerExtendFro">{{extend_hwInfo}}</span>
        </div>

        <div class="peerDeadline">
          <span>评估截至时间：</span><span id="peerDDL" style="color:red;">{{hwInfo.assessment_ddl}}</span>
        </div>



        <div class="peerHomework">
          <span>同伴作业：</span><br />

          <div v-for="(stu_hw, index) in peerHw.student_homework" :id="stu_hw.id" class="panel panel-default">
            <div class="panel-heading">
              <span class="panel-title">第{{index}}份</span>
            </div>
            <div class="panel-body">
              <p>{{stu_hw.content}}</p>
              <br />
              <div class="peerHomework-score">
                <span>分数(0-100)：</span>&nbsp;&nbsp;<input type="number" class="form-control">
                <span :class="'peerHW-score-text-error-'+stu_hw.id" style="color:red;display:none;font-size: 13px;">&nbsp;&nbsp;&nbsp;请输入0-100之间的数字！</span>
              </div>
              <div v-for="ass_sta in assessStanadrd" class="peerHomework-text">
                <span id="peerHW-text-label">{{ass_sta.standard}}</span>&nbsp;&nbsp;
                <span :class="'peerHW-text-star-'+ass_sta.id" title="">
                <img :src="stargrey" @click="changeStar(this,'#'+stu_hw.id + ' .peerHW-text-star-' + ass_sta.id)">
                <img :src="stargrey" @click="changeStar(this,'#'+stu_hw.id + ' .peerHW-text-star-' + ass_sta.id)">
                <img :src="stargrey" @click="changeStar(this,'#'+stu_hw.id + ' .peerHW-text-star-' + ass_sta.id)">
                <img :src="stargrey" @click="changeStar(this,'#'+stu_hw.id + ' .peerHW-text-star-' + ass_sta.id)">
                <img :src="stargrey" @click="changeStar(this,'#'+stu_hw.id + ' .peerHW-text-star-' + ass_sta.id)">
                <textarea class="form-control" rows="4"></textarea>
              </span>
              </div>
            </div>
          </div>

        </div>

      </div>

      <!--提交按钮-->
      <div id="peerAssessBtn" class="center-block">
        <button type="button" class="btn btn-success" @click="peerAssessSubmit()">提交</button>
        <!--重置按钮-->
        <button type="button" class="btn btn-warning" @click="peerStore()">保存</button>
      </div>

    </div>

</template>

<script type="text/javascript">
import axios from 'axios'
import store from '@/store.js'
import progressBar from '@/components/progressBar'
import stargrey from '@/assets/star-grey.png'
import staryellow from '@/assets/star-yellow.png'
import iconreback from '@/assets/reback.png'
export default {
  data () {
    return {
      homework_id: this.$route.params.id,
      hwInfo: [],
      extend_hwName: '',
      peerHw: [],
      assessStanadrd: [],
      stargrey: stargrey,
      staryellow: staryellow,
      iconreback: iconreback,
      peerData: [], // 同伴评估作业数据结果
      succ: '', // 判断是否都提交成功
      storeData: []
     /* items: [],
      classes: [],
      storeData: [] */
    }
  },
  components: {
    progressBar
  },
  methods: {
    getExtendName: function (hwid) { // 根据ID获取扩展作业来源的名字
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_info_by_id',
        method: 'post',
        data: {
          type: 'S3008',
          token: '1f5be77b086bc671b321a66ae4675330',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.extend_hwName = res.data.msg.name
        console.log(self.extend_hwName)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getHWInf: function () { // 获取作业的相关要求
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_info_by_id',
        method: 'post',
        data: {
          type: 'S3008',
          token: '1f5be77b086bc671b321a66ae4675330',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.hwInfo = res.data.msg
        this.getExtendName(self.hwInfo.extend_from)
        console.log(self.hwInfo)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getpeerHw: function () { // 获取待评估的作业
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_four_homework',
        method: 'post',
        data: {
          type: 'S3002',
          token: '3406ae3f44399522653232e2c24ac811',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.peerHw = res.data.msg
        console.log(self.peerHw)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getAssesssDimen: function () {  // 获取评估的维度及其对应文本
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_standard',
        method: 'post',
        data: {
          type: 'S3004',
          token: 'fecf5b523f9e5f7c6fc6923f653edddc',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.assessStanadrd = res.data.msg
        console.log(self.assessStanadrd)
      }).catch(function (err) {
        console.log(err)
      })
    },
    changeStar: function (element, classname) { // 控制五星评估的显示
      var obj = classname + ' img'
      var len = document.querySelector(element).prevAll(obj).length
      document.querySelector(classname).attr('title', len + 1)
      document.querySelector(obj).each(function (i, obj) {
        if (i <= len) obj.src = self.staryellow
        else {
          obj.src = self.stargrey
        }
      })
    },
    getpeerData: function () {  // 获取数据
      for (var i = 0; i < this.peerHw.student_homework.length; i++) {
        let data = {
          type: 'S3005',
          token: 'fecf5b523f9e5f7c6fc6923f653edddc',
          student_homework_id: '',
          total_score: '',
          assessment: []
        }
        data.student_homework_id = this.peerHw.student_homework[i].id
        data.total_score = document.querySelector('#' + this.peerHw.student_homework[i].id + ' input').value
        for (var j = 0; j < this.assessStanadrd.length; j++) {
          let result = {
            content: '',
            standard_id: '',
            star: ''
          }
          result.standard_id = this.assessStanadrd[j].id
          result.content = document.querySelector('.peerHW-text-star-' + this.assessStanadrd[j].id + ' textarea').value
          result.star = document.querySelector('.peerHW-text-star-' + this.assessStanadrd[j].id).attr('title')
          data.assessment.push(result)
        }
        this.peerData.push(data)
      }
    },
    peerAssessSubmit: function () { // 评估文本的提交
      this.getpeerData()
      for (var i = 0; i < 4; i++) {
        let self = this
        axios({
          url: 'https://diningx.cn/pa/public/api/student/assess_other',
          method: 'post',
          data: self.peerData[i],
          transformRequest: [function (data) {
    // Do whatever you want to transform the data
            let ret = ''
            for (let it in data) {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
            return ret
          }],
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }).then(function (res) {
          self.succ = res.data.msg
          console.log('第', i, '份评估结果提交成功')
        }).catch(function (err) {
          console.log(err)
        })
        if (this.succ !== '') {
          console.log(i, '存在提交不成功的同评数据结果')
        } else {
          store.save('hwInfo', this.hwInfo)
          store.save('extend_hwName', this.extend_hwName)
          store.save('peerData', self.peerData)
          store.save('peerHw', this.peerHw)
          this.$router.push('pages/assessment/self_assess/' + this.homework_id)
        }
      }
    },
    peerStore: function () { // 同伴评估数据的保存
      store.save('peerHw', this.peerHw)
      store.save('peerData', this.peerData)
      alert('保存成功')
    }
  },
  created () {
    this.getHWInf()
    this.getAssesssDimen()
    this.getpeerHw()
  }
}
</script>

<style type="text/css">
.link-back{
  float:right;
  width:150px;
}
.link-back img{
  float: right;
}
#content .presentIndex a,#content .presentIndex span{
  font-weight: bold;
  font-size: 12px;
  margin-bottom: 1%;
  color: #000;
  cursor: pointer;
}
/*进度条*/
#content .progressBar{
  margin-bottom: 5px;
  margin-top:10px;
  width:80%;
  margin-left:10%;
}
/*进度条标签*/
#content .progressBar-label{
  width:80%;
  margin-left:10%;
}
#content .progressBar-label li{
  list-style: none;
  float:left;
  width:20%;
  text-align: center;
  font-size: 13px;
}

/*同伴评估内容*/
#homework-peerAssess span{
  font-weight: bold;
}
#homework-peerAssess div{
  margin-top:10px;
}
/*同伴作业面板*/
#homework-peerAssess .panel-heading{
  margin-top:0px;
}
#homework-peerAssess  .panel-body{
  margin-top:0px;
}
/*分数输入框*/
#homework-peerAssess input,
#selfHomework input{
  width:100px;
  display: inline;
}

/*设置五星评分及文本*/
.peerHW-text-star-1,.peerHW-text-star-2,.peerHW-text-star-3{
  margin-left: 25px;;
}
#homework-peerAssess img
{
  width:25px;
  height:25px;
  cursor: pointer;
}
#homework-peerAssess textarea{
       width:90%;
       margin-left: 5%;
       margin-top: 10px;

}

/*提交重置按钮*/

#peerAssessBtn button{
  width:100px;
  margin-bottom:20px;
  margin-left:25%;
}



</style>


