<template>
<div>
  <router-link to="/learn_course" class="btn btn-default" style="margin-right: 900px; margin-bottom: 10px;">返回</router-link>
	<div>
	<ul class="nav nav-tabs">
  <li><router-link :to="'/pages/finished/class_brief/'+course_id">课程简介</router-link></li>
	<li><router-link :to="'/pages/finished/class_notice/'+course_id">教师公告</router-link></li>
	<li><router-link :to="'/pages/finished/class_homework/'+course_id">课程作业</router-link></li>
	<li class="active"><router-link :to="'/pages/finished/class_resources/'+course_id">课程资源</router-link></li>
	<li><router-link :to="'/pages/finished/class_showouts/'+course_id">优秀作品榜</router-link></li>
	</ul>
  </div>
		<div>
			<table class="table table-bordered">
				<thead>
								<tr>
									<th>课件名称</th>
									<th>文件大小</th>
									<th>操作</th>
								</tr>
				</thead>
				<tbody>

								<tr v-for="text in texts">
									<td>{{text.name}}</td>
									<td>{{text.size}}</td>
									<td><a href="" class="btn btn-default">下载</a></td>
								</tr>
				</tbody>
			</table>
		</div>
  
</div>
</template>
<script type="text/javascript">
import axios from 'axios'
export default {
  data () {
    return {
      course_id: this.$route.params.id,
      items: [],
      classes: [],
      texts: [],
      message: '',
      code: '',
      code_error: ''
    }
  },
  methods: {
    joinCourse: function () {
      let self = this
      console.log(self.items)
      console.log('try######______------>', self.items.name)
      axios({
        url: 'https://diningx.cn/pa/public/api/student/join_course',
        method: 'post',
        data: {
          type: 'S2001',
          token: '1f5be77b086bc671b321a66ae4675330',
          course_id: self.items.course_id,
          // class_id: '3',
          // pre_course_score: '84',
          // aim_score: '94',
          aim_text: self.message
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res)
        console.log(res.data)
      }).catch(function (err) {
        console.log(err)
        self.code_error = '该课程密码错误！请重新输入'
      })
    },
    getData: function () {
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_course_info_by_id',
        method: 'post',
        data: {
          type: 'S2003',
          token: '1f5be77b086bc671b321a66ae4675330',
          course_id: self.course_id
        },
        transformRequest: [function (data) {
        // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(self.course_id)
        console.log(res.data.msg)
        self.items = res.data.msg
        self.classes = self.items.classes
        console.log(self.classes)
        // console.log('------------->', self.items)
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.getData()
  }
}
</script>
<style type="text/css">
a {
  text-decoration: none;
}



</style>


