<template>
<div>
   <router-link to="/add_course" class="link-back"><img :src="iconreback"></router-link>
  <div>
 <label class="title">{{items.name}}问卷</label>
  </div>  
<div id="answer">


      <form @submit.prevent="submit">
<legend style="color:red;font-size:14px;width:100%;text-align: center;">注意：请认真填写该预测问卷，该预测问卷将会影响你的成长起点。</legend>
        <h4 class="text-left">1、请问你的先修课程分数是多少？  </h4>
        <input style="width: 500px;" v-model="pre_course_score" class="form-control">

        <h4 class="text-left">2、学习完这门课程后，你对自己要求的的目标绩点在哪个分数段？</h4>
        <input style="width: 500px;" v-model="aim_score" class="form-control">

        <h4 class="text-left">3、学习这门课程后，你想收获些什么？想要达到什么学习目标？</h4>
         <textarea name="goal" v-model="aim_text" style="width:950px;height:250px" placeholder="不少于200字"></textarea>
         <br>
         <br>
         
        <div style="text-align: center;">
        <button type="button" class="btn btn-default" value="submit" data-toggle="modal" data-target="#myModal1" @click="adjustfull()">提交</button>
        </div>

      </form>
      </div>
    </div>
    
</template>


<script type="text/javascript">
import axios from 'axios'
import iconreback from '@/assets/reback.png'
export default {
  data () {
    return {
      course_id: this.$route.params.course_id,
      class_id: this.$route.params.class_id,
      items: [],
      classes: [],
      class_name: '',
      pre_course_score: '',
      aim_score: '',
      aim_text: '',
      iconreback: iconreback
    }
  },
  methods: {
    adjustfull: function () {
      if (this.pre_course_score === '' || this.aim_score === '' || this.aim_text === '') {
        alert('请将信息填写完整')
      } else {
        this.joinCourse()
        this.$router.go('/learn_course')
      }
    },
    joinCourse: function () {
      let self = this
      console.log(self.items)
      console.log('try######______------>', self.items.name)
      axios({
        url: 'https://diningx.cn/pa/public/api/student/join_course',
        method: 'post',
        data: {
          type: 'S2001',
          token: 'fecf5b523f9e5f7c6fc6923f653edddc',
          course_id: self.course_id,
          class_name: self.class_name,
          pre_course_score: self.pre_course_score,
          aim_score: self.aim_score,
          aim_text: self.aim_text
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res)
        alert('添加课程成功！')
      }).catch(function (err) {
        alert('添加课程失败，请返回重试。')
        console.log(err)
      })
    },
    getData: function () {
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_course_info_by_id',
        method: 'post',
        data: {
          type: 'S2003',
          token: '1f5be77b086bc671b321a66ae4675330',
          course_id: self.course_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(self.course_id)
        console.log(res.data.msg)
        self.items = res.data.msg
        self.classes = self.items.classes
        console.log(self.classes)
        // console.log('------------->', self.items)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getclassName: function () {
      console.log(this.classes)
      for (let i = 0; i < this.classes.length; i++) {
        if (self.class_id === this.classes[i].id) {
          this.class_name = this.classes[i].class_name
        }
      }
    }
  },
  created () {
    this.getData()
    this.getclassName()
  }
}
</script>
<style type="text/css">
a {
  text-decoration: none;
}
.link-back{
  float:right;
  width:150px;
}
.link-back img{
  float: right;
}
.title{
  display: block;
  width:100%;
  text-align: center;
  color:#ffa955;
  font-size: 25px;
  margin-bottom: 10px;
}



</style>


