<template>
<div>
  <router-link to="/learn_course" class="btn btn-default" style="margin-right: 900px; margin-bottom: 10px;">返回</router-link>
  <div>
  <ul class="nav nav-tabs">
  <li><router-link :to="'/pages/learning/class_brief/'+course_id">课程简介</router-link></li>
  <li class="active"><router-link :to="'/pages/learning/class_notice/'+course_id">教师公告</router-link></li>
  <li><router-link :to="'/pages/learning/class_homework/'+course_id">课程作业</router-link></li>
  <li><router-link :to="'/pages/learning/class_resources/'+course_id">课程资源</router-link></li>
  <li><router-link :to="'/pages/learning/class_showouts/'+course_id">优秀作品榜</router-link></li>
  <li ><router-link :to="'/pages/learning/class_progress/'+course_id">教学进度表</router-link></li>
  </ul>
  </div>
  
</div>
</template>
<script type="text/javascript">
import axios from 'axios'
export default {
  data () {
    return {
      course_id: this.$route.params.id,
      items: [],
      classes: [],
      message: '',
      code: '',
      code_error: ''
    }
  },
  methods: {
    getCsCommunicate: function () {
      let self = this
      console.log(self.items)
      console.log('try######______------>', self.items.name)
      axios({
        url: 'http://127.0.0.1:8000/api/student/get_cs_communicate',
        method: 'post',
        data: {
          type: 'S2033',
          token: 'fadsfafasdafsdfsaf'
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res)
        console.log(res.data.re)
        console.log(res.data.msg[0].name + '-----' + res.data.msg.length)
        self.communications = res.data.msg
        console.log(self.communications)
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.getCsCommunicate()
  }
}
</script>
<style type="text/css">
a {
  text-decoration: none;
}



</style>


