<template>
<div style="margin-top:30px;">
  <div style="width:100% text-align: center;">
			<h1>正在学习的课程</h1>
	</div>
 <div data-spy="scroll" data-target="#navbar-example" data-offset="0" 
   style="overflow-x:visible; position: relative; margin-top:30px">
	<div style="width: 950px; float:left;">
	 <table class="table table-bordered">
		<thead>
			<tr>
				<th>课程名</th>
				<th>操作</th>
			</tr>
		</thead>
		<tbody>
		<tr  v-for="(course, index) in courses">
			<td>{{ course.course_name }}</td>
			<td><router-link :to="'/pages/learning/class_brief/'+course.id" class="btn btn-default">进入</router-link></td>
		</tr>
		</tbody>
		</table>
	</div>
	</div>
		
	</div>
</template>

<script type="text/javascript">
import axios from 'axios'
export default {
  name: 'learn_course',
  data () {
    return {
      courses: [],
      fixid: 0,
      creid: 1,
      selected_index: -1
    }
  },
  methods: {
    getAllCourse: function () {
      let self = this
      console.log('Axios')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_joined_course_list',
        method: 'post',
        data: {
          type: 'S2002',
          token: 'fecf5b523f9e5f7c6fc6923f653edddc'
        },
        transformRequest: [function (data) {
        // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('Get learning course')
        console.log(res)
        console.log(res.data.re)
        // console.log(res.data.msg[0].name + '-----' + res.data.msg.length)
        self.courses = res.data.msg// 好像没有时间判断，显示的是全部
        console.log(self.courses)
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.getAllCourse()
  }
}
</script>

<style type="text/css">
th {
	text-align: center;
}
.ax_default {
  font-family:'微软雅黑 Light', '微软雅黑 Regular', '微软雅黑';
  font-weight:290;
  font-style:normal;
  font-size:14px;
  color:#333333;
  text-align:center;
  line-height:normal;
}
.droplist {
  color:#000000;
  text-align:left;
}
.ax_default_visible, .ax_default_unplaced{
    display: none;
    visibility: visible;
}


</style>


