<template>
<div>
  <router-link to="/add_course" class="btn btn-default" style="margin-right: 900px; margin-bottom: 10px;">返回</router-link>
  <div class="detail" style="margin-top: 20px; width: 900px;">
    <h2>name{{items.name}}</h2>
  <ul class="list-unstyled">
  <li><h4 class="text-left">作业要求: <small>{{items.requirement}}</small></h4></li>
  <li><h4 class="text-left">作业修改截至时间: <small>{{items.submit_ddl}}</small></h4></li>
  <li><h4 class="text-left">我的作业: </h4><textarea name="goal" v-model="content" style="width:600px;height:300px" placeholder=""></textarea></li>
  </ul>
  </div>
   <button type="button" class="btn btn-default" @click="saveHomework()">保存</button>
  <router-link :to="'/'+items.id" class="btn btn-default" @click="submitHomework()">提交</router-link>
</div>
</template>

<script type="text/javascript">
import axios from 'axios'
import store from '@/store.js'
export default {
  data () {
    return {
      homework_id: this.$route.params.id,
      items: [],
      content: ''
    }
  },
  methods: {
    saveHomework: function () {
      store.save('contentElement', this.content)
      alert('保存成功')
    },
    submitHomework: function () {
      let self = this
      console.log('try######______------>', self.items.name)
      axios({
        url: 'https://diningx.cn/pa/public/api/student/submit_homework',
        method: 'post',
        data: {
          type: 'S3001',
          token: '1f5be77b086bc671b321a66ae4675330',
          homework_id: self.homework_id,
          content: self.content
        },
        transformRequest: [function (data) {
        // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res)
        console.log(res.data)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getData: function () {
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_info_by_id',
        method: 'post',
        data: {
          type: 'S3008',
          token: '1f5be77b086bc671b321a66ae4675330',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
          // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('Get HW')
        console.log(res)
        console.log(self.homework_id)
        self.items = res.data.msg
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.getData()
  }
}
</script>


<style type="text/css">
a {
  text-decoration: none;
}



</style>


