<template>
<div>
  <router-link to="/add_course" class="btn btn-default" style="margin-right: 900px; margin-bottom: 10px;">返回</router-link>
  <div class="detail" style="margin-top: 20px; width: 900px;">
    <h2>{{items.name}}</h2>
  <ul class="list-unstyled">
  <li><h4 class="text-left">作业要求: <small>{{items.requirement}}</small></h4></li>
  <li><h4 class="text-left">作业修改截至时间: <small>{{items.modify_ddl}}</small></h4></li>
  <li><h4 class="text-left">评估标准: <small>undefined</small></h4></li>
  <li><h4 class="text-left">我的原作业: <small>undefined</small></h4></li>
  <li><h4 class="text-left">同伴评估结果: <small>undefined</small></h4></li>
  <li><h4 class="text-left">修改后的作业: </h4><input v-model="content" class="form-control"></li>
  </ul>
  </div>
  <button type="button" class="btn btn-default" @click="modifyHomework()">提交</button>
</div>
</template>
<script type="text/javascript">
import axios from 'axios'
export default {
  data () {
    return {
      homework_id: this.$route.params.id,
      items: [],
      content: ''
    }
  },
  methods: {
    modifyHomework: function () {
      // TODO
      let self = this
      console.log(self.items)
      console.log('try######______------>', self.items.name)
      axios({
        url: 'https://diningx.cn/pa/public/api/student/join_course',
        method: 'post',
        data: {
          type: 'S2001',
          token: '1f5be77b086bc671b321a66ae4675330',
          course_id: self.items.course_id,
          aim_text: self.message
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res)
        console.log(res.data)
      }).catch(function (err) {
        console.log(err)
        self.code_error = '该课程密码错误！请重新输入'
      })
    },
    getData: function () {
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_info_by_id',
        method: 'post',
        data: {
          type: 'S3008',
          token: '1f5be77b086bc671b321a66ae4675330',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
        // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('Get hw')
        console.log(res)
        console.log(self.homework_id)
        console.log(res.data.msg)
        self.items = res.data.msg
        // console.log('------------->', self.items)
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.getData()
  }
}
</script>
<style type="text/css">
a {
  text-decoration: none;
}



</style>


