<template>
  <div class="topbar">
     <div class="logo">
        <span class="navbar-brand" href="#">同伴评估系统</span>
      </div>

      <!--用户信息-->
      <div id="nav-right">
        <ul class="user-info">
          <li>
            <a class="setup-icon navbar-brand"><img :src="usersetup"></a>
          </li>
          <li>
            <a class="notice-icon navbar-brand"><img :src="userbell"></a>
          </li>
          <li>
            <a class="username navbar-brand">Carrie</a>
          </li>
          <li>
            <a class="user-photo navbar-brand"><img class="img-circle" :src="userPhoto" /></a>
          </li>
        </ul>
      </div>
    
  </div>

</template>

<script type="text/javascript">
import userPhoto from '@/assets/user-photo.jpg'
import userbell from '@/assets/bell.png'
import usersetup from '@/assets/setup.png'
export default {
  data () {
    return {
      userPhoto: userPhoto,
      userbell: userbell,
      usersetup: usersetup
    }
  }
}
</script>

<style type="text/css">
.topbar {
  border-bottom: solid #D5D5D5;
  background-color: #F5F5F5;
  width: 100%;
  height: 55px;
}	
.logo {
  width: 60%;
  float: left;
}

.logo span {
  font-size: 25px;
  float: right;
}

#nav-right {
  width: 40%;
  float: left;
}

#nav-right li {
  float: right;
  list-style: none;
}

#nav-right .user-photo {
  padding: 10px;
}

#nav-right .user-photo img {
  width: 35px;
  height: 35px;
}

#nav-right .username {
  padding-left: 0px;
  font-weight: bold;
}

#nav-right .notice-icon img {
  width: 20px;
  height: 20px;
}

#nav-right .setup-icon img {
  width: 20px;
  height: 20px;
}

</style>


