<template>
 <div>
   <!--进度条-->
      <div class="progress progressBar">
        <div id="progress-green" class="progress-bar progress-bar-success progress-bar-striped" v-bind:style="{ width: greenwidth }">
        </div>
        <div id="progress-orange" class="progress-bar progress-bar-warning progress-bar-striped" style="width:20%;">
        </div>
      </div>
      <!--进度条标签-->
      <ul class="progressBar-label">
        <li>提交作业</li>
        <li>同伴评估</li>
        <li>自我评估</li>
        <li>作业修改</li>
        <li>作业归档</li>
      </ul>
 </div> 
</template>
<script type="text/javascript">
export default {
  props: ['greenwidth']
}
</script>
<style type="text/css">
.progressBar{
  margin-bottom: 5px;
  margin-top:10px;
  width:80%;
  margin-left:10%;
}
/*进度条标签*/
.progressBar-label{
  width:80%;
  margin-left:10%;
}
.progressBar-label li{
  list-style: none;
  float:left;
  width:20%;
  text-align: center;
  font-size: 13px;
}

</style>


