<template>
<div>
    <router-link to="/add_course" class="link-back"><img :src="iconreback"></router-link>
    

  
  <div>
  <ul class="nav nav-tabs">
  <li class="active"><router-link :to="'/pages/classDetail/'+course_id">课程详情</router-link></li>
  </ul>
  </div>
  <div class="detail" style="margin-top: 20px; width: 900px;">
  <ul class="list-unstyled">
  <li><h4 class="text-left">课程名: <small>{{items.name}}</small></h4></li>
  <li><h4 class="text-left">授课老师: <small>{{items.teacher_name}}</small></h4></li>
  <li><h4 class="text-left">上课班级: <small>{{class_name}}&nbsp;&nbsp;</small></h4></li>
  <li><h4 class="text-left">课程介绍: <small>{{items.description}}</small></h4></li>
  <li><h4 class="text-left">课程时长: <small>{{items.total_hours}}</small></h4></li>
  <li><h4 class="text-left">开课时间段: <small>{{items.start_day}}---{{ items.end_day }}</small></h4></li>
  <li><h4 class="text-left">先修课程: <small>{{items.pre_course_name}}</small></h4></li>
  <li><h4 class="text-left">教学目标: <small>{{items.aim}}</small></h4></li>
  </ul>
  </div>
  <router-link :to="'/pages/classAsk/'+course_id+'/'+class_id" class="btn btn-default">添加课程</router-link>

</div>
</template>
<script type="text/javascript">
import axios from 'axios'
import iconreback from '@/assets/reback.png'
export default {
  data () {
    return {
      course_id: this.$route.params.course_id,
      class_id: this.$route.params.class_id,
      items: [],
      classes: [],
      class_name: '',
      iconreback: iconreback
    }
  },
  methods: {
    getData: function () {
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_course_info_by_id',
        method: 'post',
        data: {
          type: 'S2003',
          token: '1f5be77b086bc671b321a66ae4675330',
          course_id: self.course_id
        },
        transformRequest: [function (data) {
        // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res.data.re)
        console.log('<------S2003', res.data.msg)
        console.log('课程名')
        console.log(res.data.msg.name)
        self.items = res.data.msg
        self.classes = res.data.msg.classes
        console.log(self.classes)
        // console.log('------------->', self.items)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getclassName: function () {
      for (let i = 0; i < this.classes.length; i++) {
        if (this.class_id === this.classes[i].id) {
          this.class_name = this.classes[i].class_name
        }
      }
    }
  },
  created () {
    this.getData()
  }
}
</script>
<style type="text/css">
a {
  text-decoration: none;
}

.link-back{
  float:right;
  width:150px;
}
.link-back img{
  float: right;
}

</style>


