<template>
<div>
<div style="font-weight: bold;font-size: 20px;margin-bottom: 1%;color:#ffa955;">我的同评作业</div>

      <div class="courseTab">
        <!--选项卡标签 -->
        <ul id="content-tab-header" class="nav nav-tabs" role="tablist">
          <li v-for="(cous, index) in coushomework" role="presentation" v-bind:class="{active: index === 1}">
            <router-link :to="'#'+cous.homeworks[0].course_id" aria-controls="learningcou.course_id" role="tab" data-toggle="tab">{{cous.course_name}}</router-link>
          </li>

        </ul>

        <!--选项卡内容 -->
        <div id="content-tab-body" class="tab-content">

          <div v-for="(coursehw,index) in coushomework" role='tabpanel' class='tab-pane content-tab-body-padding' v-bind:class="{active: index === 1}" :id="coursehw.homeworks[0].course_id">
            <div id="HWlist" class='table-responsive'>
              <table class='table table-striped'>
                <tr>
                  <th>作业标题</th>
                  <th>轮次</th>
                  <th>作业状态</th>
                  <th>截至时间</th>
                  <th>已交人数</th>
                  <th>我的状态</th>
                  <th>操作</th>
                </tr>
                 <tr v-for="(homework, index) in coursehw.homeworks">
                   <td>{{homework.name}}</td>
                   <td>{{homework.round}}</td>
                   <td>{{gethwState(homework.state)}}</td>
                   <td>{{gethwddl(homework)}}</td>
                   <td>{{homework.submit_num}}</td>
                   <td>{{homework.student_homework_state}}</td>
                   <td>
                   <!-- 提交阶段 -->
                    <div v-if="homework.state === 1 && homework.student_homework_state === 0">
                      <router-link :to="'/pages/assessment/write_hw/' + homework.id" >查看</router-link>
                    </div>
                  <!--   <div v-else-if="homework.hw_state === 1 && homework.stu_hw_state === 1">
                      <router-link :to="'/pages/assessment/write_hw/'+homework.hw_id" >查看</router-link>
                    </div> -->

                     <!-- 评估阶段 -->
                    <div v-else-if="homework.state === 2 &&homework.student_homework_state ===0">
                      <router-link :to="'/pages/assessment/peer_assess/'+homework.id" >查看</router-link>
                    </div>
                    <div v-else-if="homework.state === 2 &&homework.student_homework_state ===1">
                      <router-link  :to="'/pages/assessment/assessOver/'+homework.id" >查看</router-link>
                    </div>

                     <!-- 修改阶段 -->
                    <!-- <div v-else-if="homework.hw_state === 3 && homework.stu_hw_state === 0">
                     <router-link :to="'/pages/assessment/modify'+homework.hw_id" >查看</router-link>
                    </div> -->
                    <div v-else>
                     <router-link :to="'/pages/assessment/modify'+homework.id" >查看</router-link>
                    </div>
                   </td>
               </tr>
              </table>
            </div>
          </div>

        </div>

      </div>
</div>
</template>

<script type="text/javascript">
import axios from 'axios'
export default {
  name: 'assess_list',
  data () {
    return {
      temp: [], // 暂存根据id获取课程信息
      learningcous: [],
      coushomework: []
    }
  },
  methods: {
    // 根据课程id获取课程信息，需要课程名name，课程状态state
    getCouInfo: function (couid) {
      // let self = this
      console.log('根据课程id获取课程信息')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_course_info_by_id',
        method: 'post',
        data: {
          type: 'S2003',
          token: '1f5be77b086bc671b321a66ae4675330',
          course_id: couid
        },
        transformRequest: [function (data) {
        // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res)
        console.log(res.data.re)
        console.log(res.data.msg.name)
        return res.data.msg
      }).catch(function (err) {
        console.log(err)
      })
    },
    // 获取参与所有课程，通过判断，获取当前正在学习的课程和对应正在进行的作业项
    getLearningCourse: function () {
      console.log('获取所有参与课程')
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_joined_course_list',
        method: 'post',
        data: {
          type: 'S2002',
          token: 'fecf5b523f9e5f7c6fc6923f653edddc'
        },
        transformRequest: [function (data) {
        // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res)
        console.log(res.data.re)
        console.log(res.data.msg)
        for (var i = 0; i < res.data.msg.length; i++) {
          let courseID = res.data.msg[i].course_id
          console.log('筛选正在进行的课程')
          console.log(courseID)
          let courseIDInfo = self.getCouInfo(courseID)
          if (courseIDInfo.state === 1) {
            self.learningcous.push(courseID)
          }
        }
        console.log('筛选出正在进行的课程ID')
      }).catch(function (err) {
        console.log(err)
      })
    },
    // 获取正在进行的课程的全部作业列表
    getHwList: function () {
      console.log('获取正在学习的课程的作业列表')
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_list_by_time',
        method: 'post',
        data: {
          type: 'S3007',
          token: 'fecf5b523f9e5f7c6fc6923f653edddc'
        },
        transformRequest: [function (data) {
        // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res)
        console.log(res.data.re)
        console.log(res.data.msg)
        let list = res.data.msg
        for (var k = 0; k < self.learningcous.length; k++) {
          for (var i = 0; i < list.length; i++) {
            if (self.learningcous[k].toString() === list[i].homeworks[0].course_id.toString()) {
              self.coushomework.push(list[i])
            }
          }
        }
        console.log('已经筛选出正在进行的课程的作业项')
      }).catch(function (err) {
        console.log(err)
      })
    },
    // 根据state返回当前阶段的文字
    gethwState: function (state) {
      if (state === 1) {
        return '提交阶段'
      } else if (state === 2) {
        return '互评阶段'
      } else if (state === 3) {
        return '修改阶段'
      } else {
        return '已归档'
      }
    },
    // 根据作业的状态返回该作业的截止时间
    gethwddl: function (homework) {
      if (homework.state === 1) {
        return homework.submit_ddl
      } else if (homework.state === 2) {
        return homework.assessment_ddl
      } else if (homework.state === 3) {
        return homework.modify_ddl
      } else {
        return '--'
      }
    }
  },
  created () {
    this.getLearningCourse()
    console.log('over----------getLearningCourse()')
    this.getHwList()
    console.log('over----------getHwList()')
  }
}

</script>

<style type="text/css">
#course{
  margin-top: 2%;
}
#content #content-tab-body .content-tab-body-padding{
  padding-top:10px;
}

/*选项卡表格居中*/
#content-tab-body table tr th,#content-tab-body table tr td{
  text-align: center;
}


</style>


