import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import AddCourse from '@/components/AddCourse'
import LearnCourse from '@/components/LearningCourse'
import FinishCourse from '@/components/FinishedCourse'
import classdetail from '@/pages/classDetail'
import classask from '@/pages/classAsk'
import LearnClassBrief from '@/pages/learning/classBrief'
import LearnClassHomework from '@/pages/learning/classHomework'
import LearnClassNotice from '@/pages/learning/classNotice'
import LearnClassProgress from '@/pages/learning/classProgress'
import LearnClassResources from '@/pages/learning/classResources'
import LearnClassShowouts from '@/pages/learning/classShowouts'
import FinishClassBrief from '@/pages/finished/classBrief'
import FinishClassHomework from '@/pages/finished/classHomework'
import FinishClassNotice from '@/pages/finished/classNotice'
import FinishClassResources from '@/pages/finished/classResources'
import FinishClassShowouts from '@/pages/finished/classShowouts'
import HomeworkInfo from '@/pages/homework/homeworkInfo'
import HomeworkSubmit from '@/pages/homework/homeworkSubmit'
import AssessmentList from '@/components/assessList'
import WriteHw from '@/pages/assessment/writeHw'
import PeerAssess from '@/pages/assessment/peerAssess'
import SelfAssess from '@/pages/assessment/selfAssess'
import Modify from '@/pages/assessment/modify'
import assessOver from '@/pages/assessment/assessOver'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/a',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/add_course',
      name: 'Add Course',
      component: AddCourse
    },
    {
      path: '/learn_course',
      name: 'Learn Course',
      component: LearnCourse
    },
    {
      path: '/finish_course',
      name: 'Finish Course',
      component: FinishCourse
    },
    {
      path: '/pages/classDetail/:course_id/:class_id',
      name: 'classdetail',
      component: classdetail
    },
    {
      path: '/pages/classAsk/:course_id/:class_id',
      name: 'classask',
      component: classask
    },
    {
      path: '/pages/learning/class_brief/:id',
      name: 'ClassBrief',
      component: LearnClassBrief
    },
    {
      path: '/pages/learning/class_homework/:id',
      name: 'ClassHomework',
      component: LearnClassHomework
    },
    {
      path: '/pages/learning/class_notice/:id',
      name: 'ClassNotice',
      component: LearnClassNotice
    },
    {
      path: '/pages/learning/class_progress/:id',
      name: 'ClassProgress',
      component: LearnClassProgress
    },
    {
      path: '/pages/learning/class_resources/:id',
      name: 'ClassResources',
      component: LearnClassResources
    },
    {
      path: '/pages/learning/class_showouts/:id',
      name: 'ClassShowouts',
      component: LearnClassShowouts
    },
    {
      path: '/pages/finished/class_brief/:id',
      name: 'ClassBrief',
      component: FinishClassBrief
    },
    {
      path: '/pages/finished/class_homework/:id',
      name: 'ClassHomework',
      component: FinishClassHomework
    },
    {
      path: '/pages/finished/class_notice/:id',
      name: 'ClassNotice',
      component: FinishClassNotice
    },
    {
      path: '/pages/finished/class_resources/:id',
      name: 'ClassResources',
      component: FinishClassResources
    },
    {
      path: '/pages/finished/class_showouts/:id',
      name: 'ClassShowouts',
      component: FinishClassShowouts
    },
    {
      path: '/pages/homework/homework_info/:id',
      name: 'HomeworkInfo',
      component: HomeworkInfo
    },
    {
      path: '/pages/homework/homework_submit/:id',
      name: 'HomeworkSubmit',
      component: HomeworkSubmit
    },
    {
      path: '/assess_list',
      name: 'AssessmentList',
      component: AssessmentList
    },
    {
      path: '/pages/assessment/write_hw/:id',
      name: 'WriteHw',
      component: WriteHw
    },
    {
      path: '/pages/assessment/peer_assess/:id',
      name: 'PeerAssess',
      component: PeerAssess
    },
    {
      path: '/pages/assessment/self_assess/:id',
      name: 'SelfAssess',
      component: SelfAssess
    },
    {
      path: '/pages/assessment/modify/:id',
      name: 'Modify',
      component: Modify
    },
    {
      path: '/pages/assessment/assessOver/:id',
      name: 'assessOver',
      component: assessOver
    }
  ]
})




