<template>
<div id="content">
  <router-link to="/assess_list" class="link-back"><img :src="iconreback"></router-link>

      <!-- 进度条 -->
      <progressBar greenwidth='60%'></progressBar>
      <!--标题-->
      <br />
      <div id="asoverName" style="margin:10px auto;font-size: 18px;font-weight: bold;text-align: center;">{{hwInfo.name}}</div>

      <div id="homework-asoverAssess">

        <div class="asoverRequest">
          <span>作业要求：</span><span id="asoverReq">{{hwInfo.requirement}}</span>
        </div>

        <div class="asoverRound">
          <span>评估轮次：</span><span id="asoverround">{{hwInfo.round}}</span>
        </div>

        <div class="asoverExtendFrom">
          <span>源自：</span><span id="asoverExtendFro">{{extend_hwInfo}}</span>
        </div>

        <div class="asoverDeadline">
          <span>评估截至时间：</span><span id="asoverDDL" style="color:red;">{{hwInfo.assessment_ddl}}</span>
        </div>



        <div class="asoverHomework">
          <span>我的评估作业：</span><br />

          <!-- 同评 -->
          <div v-for="(peer, indexOut) in asoverData[0]">
          <div :id="peer.student_homework_id" class="panel panel-default">
            <div class="panel-heading">
              <span class="panel-title">同伴{{$indexOut}}</span>
            </div>
            <div class="panel-body">
              <p>{{asoverrHw.student_homework[indexOut].content}}</p>
              <br />
              <div class="asoverHomework-score">
                <span>分数(0-100)：</span>&nbsp;&nbsp;<label class="form-control">{{peer.total_score}}</label>
              </div>
              <div v-for="(ass_sta, indexIn) in assessStanadrd" class="asoverHomework-text">
                <span id="asoverHW-text-label">{{ass_sta.standard}}</span>&nbsp;&nbsp;
                <span :class="'asoverHW-text-star-'+ass_sta.id">
                {{peer.assessment[indexIn].star}}
                </span>
                <p class="form-control">{{peer.assessment[indexIn].content}}</p>
              </span>
              </div>
            </div>
          </div>
          </div>


         <!-- 自评 -->
          <div :id="asoverData[1].student_homework_id" class="panel panel-default">
            <div class="panel-heading">
              <span class="panel-title">我的自评</span>
            </div>
            <div class="panel-body">
              <p>{{asoverHw.student_homework_content}}</p>
              <br />
              <div class="asoverHomework-score">
                <span>分数(0-100)：</span>&nbsp;&nbsp;<label class="form-control">{{asoverData[1].total_score}}</label>
              </div>
              <div v-for="(ass_sta, index) in assessStanadrd" class="asoverHomework-text">
                <span id="asoverHW-text-label">{{ass_sta.standard}}</span>&nbsp;&nbsp;
                <span :class="'asoverHW-text-star-' + ass_sta.id">
                {{asoverData[1].assessment[index].star}}
                </span>
                <p class="form-control">{{asoverData[1].assessment[index].content}}</p>
              </div>
            </div>
          </div>


        </div>

      </div>


    </div>

</template>

<script type="text/javascript">
import axios from 'axios'
import store from '@/store.js'
import progressBar from '@/components/progressBar'
import stargrey from '@/assets/star-grey.png'
import staryellow from '@/assets/star-yellow.png'
import iconreback from '@/assets/reback.png'
export default {
  data () {
    return {
      homework_id: this.$route.params.id,
      hwInfo: [],
      extend_hwName: '',
      asoverHw: [],
      assessStanadrd: [],
      stargrey: stargrey,
      staryellow: staryellow,
      iconreback: iconreback,
      asoverData: [],
      succ: '', // 判断是否都提交成功
      storeData: []
     /* items: [],
      classes: [],
      storeData: [] */
    }
  },
  components: {
    progressBar
  },
  methods: {
    showpeerData: function () {
      this.asoverHw = store.fetch('peerHw')
      this.asoverData[0] = store.fetch('peerData')
      this.asoverData[2] = store.fetch('selfData')
    },
    getExtendName: function (hwid) { // 根据ID获取扩展作业来源的名字
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_info_by_id',
        method: 'post',
        data: {
          type: 'S3008',
          token: '1f5be77b086bc671b321a66ae4675330',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.extend_hwName = res.data.msg.name
        console.log(self.extend_hwName)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getHWInf: function () { // 获取作业的相关要求
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_info_by_id',
        method: 'post',
        data: {
          type: 'S3008',
          token: '1f5be77b086bc671b321a66ae4675330',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.hwInfo = res.data.msg
        this.getExtendName(self.hwInfo.extend_from)
        console.log(self.hwInfo)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getAssesssDimen: function () {  // 获取评估的维度及其对应文本
      let asover = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_standard',
        method: 'post',
        data: {
          type: 'S3004',
          token: 'fecf5b523f9e5f7c6fc6923f653edddc',
          homework_id: asover.homework_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        asover.assessStanadrd = res.data.msg
        console.log(asover.assessStanadrd)
      }).catch(function (err) {
        console.log(err)
      })
    },
    asoverStore: function () { // 同伴评估数据的保存
      store.save('asoverData', this.asoverData)
      alert('保存成功')
    }
  },
  created () {
    this.getHWInf()
    this.getAssesssDimen()
    this.showpeerData()
  }
}
</script>

<style type="text/css">
#content .presentIndex a,#content .presentIndex span{
  font-weight: bold;
  font-size: 12px;
  margin-bottom: 1%;
  color: #000;
  cursor: pointer;
}
/*进度条*/
#content .progressBar{
  margin-bottom: 5px;
  margin-top:10px;
  width:80%;
  margin-left:10%;
}
/*进度条标签*/
#content .progressBar-label{
  width:80%;
  margin-left:10%;
}
#content .progressBar-label li{
  list-style: none;
  float:left;
  width:20%;
  text-align: center;
  font-size: 13px;
}

/*同伴评估内容*/
#homework-asoverAssess span{
  font-weight: bold;
}
#homework-asoverAssess div{
  margin-top:10px;
}
/*同伴作业面板*/
#homework-asoverAssess .panel-heading{
  margin-top:0px;
}
#homework-asoverAssess  .panel-body{
  margin-top:0px;
}
/*分数输入框*/
#homework-asoverAssess input,
#asoverHomework input{
  width:100px;
  display: inline;
}

/*设置五星评分及文本*/
.asoverHW-text-star-1,.asoverHW-text-star-2,.asoverHW-text-star-3{
  margin-left: 25px;;
}
#homework-asoverAssess img
{
  width:25px;
  height:25px;
  cursor: pointer;
}
#homework-asoverAssess textarea{
       width:90%;
       margin-left: 5%;
       margin-top: 10px;

}

/*提交重置按钮*/

#asoverAssessBtn button{
  width:100px;
  margin-bottom:20px;
  margin-left:25%;
}


.link-back{
  float:right;
  width:150px;
}
.link-back img{
  float: right;
}

</style>


