<template>
<div>
   <div class="link-back">
      <router-link to="/learn_course" class="btn btn-default"><img :src="iconreback"></router-link>
    </div>
	<div>
	<ul class="nav nav-tabs">
  <li><router-link :to="'/pages/learning/class_brief/'+course_id">课程简介</router-link></li>
	<li><router-link :to="'/pages/learning/class_notice/'+course_id">教师公告</router-link></li>
	<li class="active"><router-link :to="'/pages/learning/class_homework/'+course_id">课程作业</router-link></li>
	<li><router-link :to="'/pages/learning/class_resources/'+course_id">课程资源</router-link></li>
	<li><router-link :to="'/pages/learning/class_showouts/'+course_id">优秀作品榜</router-link></li>
	<li><router-link :to="'/pages/learning/class_progress/'+course_id">教学进度表</router-link></li>
	</ul>
  </div>
  <div id="homework">
				<div data-spy="scroll" data-target="#navbar-example" data-offset="0" style="overflow-x:hidden; position: relative;">
					<div style="width: 720px;">
						<table class="table table-bordered">
							<thead>
								<tr>
									<th>作业标题</th>
									<th>截止时间</th>
									<th>作业状态</th>
									<th>提交人数</th>
									<th>操作</th>
								</tr>
							</thead>
							<tbody>

								<tr v-for="homework in homeworks">
									<td>{{homework.name}}</td>
									<td>{{homework.submit_ddl}}</td>
									<td>{{homework.state}}</td>
									<td>{{homework.submit_num}}</td>
									<td>
										<div v-if="homework.state =='1'">
                    <router-link :to="'/pages/assessment/writeHw/'+course_id" class="btn btn-default">进入写作</router-link>   
                    </div>
										<div v-else>
										<router-link :to="'/pages/homework/homework_info/'+course_id" class="btn btn-default">查看详情</router-link>
										</div>
									</td>

								</tr>

							</tbody>
						</table>
					</div>
				</div>
	</div>
  
</div>
</template>
<script type="text/javascript">
import axios from 'axios'
import iconreback from '@/assets/reback.png'
export default {
  data () {
    return {
      course_id: this.$route.params.id,
      items: [],
      homeworks: [],
      classes: [],
      message: '',
      code: '',
      code_error: '',
      iconreback: iconreback
    }
  },
  methods: {
    joinCourse: function () {
      let self = this
      console.log(self.items)
      console.log('try######______------>', self.items.name)
      axios({
        url: 'https://diningx.cn/pa/public/api/student/join_course',
        method: 'post',
        data: {
          type: 'S2001',
          token: '1f5be77b086bc671b321a66ae4675330',
          course_id: self.items.course_id,
          // class_id: '3',
          // pre_course_score: '84',
          // aim_score: '94',
          aim_text: self.message
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res)
        console.log(res.data)
      }).catch(function (err) {
        console.log(err)
        self.code_error = '该课程密码错误！请重新输入'
      })
    },
    getData: function () {
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_list_by_time',
        method: 'post',
        data: {
          type: 'S3007',
          token: 'fecf5b523f9e5f7c6fc6923f653edddc'
        },
        transformRequest: [function (data) {
        // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(self.course_id)
        console.log(res.data.msg)
        for (var i = 0; i < res.data.msg.length; i++) {
          if (res.data.msg[i].homeworks[0].course_id.toString() === self.course_id.toString()) {
            self.homeworks.push(res.data.msg[i].homeworks)
          }
        }
        // self.homeworks = res.data.msg
        console.log('------------->', self.homeworks)
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.getData()
  }
}
</script>
<style type="text/css">
a {
  text-decoration: none;
}
.link-back{
  float:right;
}


</style>


