<template>
<div id="content">
  <router-link to="/assess_list" class="link-back"><img :src="iconreback"></router-link>
      <!-- 进度条 -->
      <progressBar greenwidth='40%'></progressBar>
      <!--标题-->
      <br />
      <div id="selfName" style="margin:10px auto;font-size: 18px;font-weight: bold;text-align: center;">{{hwInfo.name}}</div>

      <div id="homework-selfAssess">

        <div class="selfRequest">
          <span>作业要求：</span><span id="selfReq">{{hwInfo.requirement}}</span>
        </div>

        <div class="selfRound">
          <span>评估轮次：</span><span id="selfround">{{hwInfo.round}}</span>
        </div>

        <div class="selfExtendFrom">
          <span>源自：</span><span id="selfExtendFro">{{extend_hwInfo}}</span>
        </div>

        <div class="selfDeadline">
          <span>评估截至时间：</span><span id="selfDDL" style="color:red;">{{hwInfo.assessment_ddl}}</span>
        </div>



        <div class="selfHomework">
          <span>同伴作业：</span><br />

          <div :id="selfHw.student_homework_id" class="panel panel-default">
            <div class="panel-heading">
              <span class="panel-title">我的作业</span>
            </div>
            <div class="panel-body">
              <p>{{selfHw.student_homework_content}}</p>
              <br />
              <div class="selfHomework-score">
                <span>分数(0-100)：</span>&nbsp;&nbsp;<input type="number" class="form-control">
                <span :class="'selfHW-score-text-error-'+stu_hw.id" style="color:red;display:none;font-size: 13px;">&nbsp;&nbsp;&nbsp;请输入0-100之间的数字！</span>
              </div>
              <div v-for="ass_sta in assessStanadrd" class="selfHomework-text">
                <span id="selfHW-text-label">{{ass_sta.standard}}</span>&nbsp;&nbsp;
                <span :class="'selfHW-text-star-'+ass_sta.id" title="">
                <img :src="stargrey" @click="changeStar(this, ' .selfHW-text-star-' + ass_sta.id)">
                <img :src="stargrey" @click="changeStar(this, ' .selfHW-text-star-' + ass_sta.id)">
                <img :src="stargrey" @click="changeStar(this, ' .selfHW-text-star-' + ass_sta.id)">
                <img :src="stargrey" @click="changeStar(this, ' .selfHW-text-star-' + ass_sta.id)">
                <img :src="stargrey" @click="changeStar(this, ' .selfHW-text-star-' + ass_sta.id)">
                <textarea class="form-control" rows="4"></textarea>
              </span>
              </div>
            </div>
          </div>

        </div>

      </div>

      <!--提交按钮-->
      <div id="selfAssessBtn" class="center-block">
        <button type="button" class="btn btn-success" @click="selfAssessSubmit()">提交</button>
        <!--重置按钮-->
        <button type="button" class="btn btn-warning" @click="selfStore()">保存</button>
      </div>

    </div>

</template>

<script type="text/javascript">
import axios from 'axios'
import store from '@/store.js'
import progressBar from '@/components/progressBar'
import stargrey from '@/assets/star-grey.png'
import staryellow from '@/assets/star-yellow.png'
import iconreback from '@/assets/reback.png'
export default {
  data () {
    return {
      homework_id: this.$route.params.id,
      hwInfo: [],
      extend_hwName: '',
      selfHw: [],
      assessStanadrd: [],
      stargrey: stargrey,
      staryellow: staryellow,
      selfData: [],
      iconreback: iconreback,
      succ: '', // 判断是否都提交成功
      storeData: []
     /* items: [],
      classes: [],
      storeData: [] */
    }
  },
  components: {
    progressBar
  },
  methods: {
    getExtendName: function (hwid) { // 根据ID获取扩展作业来源的名字
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_info_by_id',
        method: 'post',
        data: {
          type: 'S3008',
          token: '1f5be77b086bc671b321a66ae4675330',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.extend_hwName = res.data.msg.name
        console.log(self.extend_hwName)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getHWInf: function () { // 获取作业的相关要求
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_info_by_id',
        method: 'post',
        data: {
          type: 'S3008',
          token: '1f5be77b086bc671b321a66ae4675330',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.hwInfo = res.data.msg
        this.getExtendName(self.hwInfo.extend_from)
        console.log(self.hwInfo)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getselfHw: function () { // 获取待评估的作业
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_four_homework',
        method: 'post',
        data: {
          type: 'S3002',
          token: '3406ae3f44399522653232e2c24ac811',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.selfHw = res.data.msg
        console.log(self.selfHw)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getAssesssDimen: function () {  // 获取评估的维度及其对应文本
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_standard',
        method: 'post',
        data: {
          type: 'S3004',
          token: 'fecf5b523f9e5f7c6fc6923f653edddc',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.assessStanadrd = res.data.msg
        console.log(self.assessStanadrd)
      }).catch(function (err) {
        console.log(err)
      })
    },
    changeStar: function (element, classname) { // 控制五星评估的显示
      var obj = classname + ' img'
      var len = document.querySelector(element).prevAll(obj).length
      document.querySelector(classname).attr('title', len + 1)
      document.querySelector(obj).each(function (i, obj) {
        if (i <= len) obj.src = self.staryellow
        else {
          obj.src = self.stargrey
        }
      })
    },
    getselfData: function () {  // 获取数据
      this.selfData = []  // 置空self_data
      let data = {
        type: 'S3003',
        token: 'fecf5b523f9e5f7c6fc6923f653edddc',
        student_homework_id: '',
        total_score: '',
        assessment: []
      }
      data.student_homework_id = this.selfHw.student_homework_id
      data.total_score = document.querySelector('#' + this.selfHw.student_homework_id + ' input').value
      for (var j = 0; j < this.assessStanadrd.length; j++) {
        let result = {
          content: '',
          standard_id: '',
          star: ''
        }
        result.standard_id = this.assessStanadrd[j].id
        result.content = document.querySelector('.selfHW-text-star-' + this.assessStanadrd[j].id + ' textarea').value
        result.star = document.querySelector('.selfHW-text-star-' + this.assessStanadrd[j].id).attr('title')
        data.assessment.push(result)
      }
      this.selfData.push(data)
    },
    selfAssessSubmit: function () { // 评估文本的提交
      this.getselfData()
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/assess_myself',
        method: 'post',
        data: self.selfData[0],
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        self.succ = res.data.msg
        console.log(self.succ)
        console.log('自评评估结果提交成功')
        self.$router.push('/pages/assessment/assessOver' + self.homework_id)
      }).catch(function (err) {
        console.log(err)
      })
    },
    selfStore: function () { // 同伴评估数据的保存
      store.save('selfData', this.selfData)
      alert('保存成功')
    }
  },
  created () {
    this.getHWInf()
    this.getAssesssDimen()
    this.getselfHw()
  }
}
</script>

<style type="text/css">
.link-back{
  float:right;
  width:150px;
}
.link-back img{
  float: right;
}
#content .presentIndex a,#content .presentIndex span{
  font-weight: bold;
  font-size: 12px;
  margin-bottom: 1%;
  color: #000;
  cursor: pointer;
}
/*进度条*/
#content .progressBar{
  margin-bottom: 5px;
  margin-top:10px;
  width:80%;
  margin-left:10%;
}
/*进度条标签*/
#content .progressBar-label{
  width:80%;
  margin-left:10%;
}
#content .progressBar-label li{
  list-style: none;
  float:left;
  width:20%;
  text-align: center;
  font-size: 13px;
}

/*同伴评估内容*/
#homework-selfAssess span{
  font-weight: bold;
}
#homework-selfAssess div{
  margin-top:10px;
}
/*同伴作业面板*/
#homework-selfAssess .panel-heading{
  margin-top:0px;
}
#homework-selfAssess  .panel-body{
  margin-top:0px;
}
/*分数输入框*/
#homework-selfAssess input,
#selfHomework input{
  width:100px;
  display: inline;
}

/*设置五星评分及文本*/
.selfHW-text-star-1,.selfHW-text-star-2,.selfHW-text-star-3{
  margin-left: 25px;;
}
#homework-selfAssess img
{
  width:25px;
  height:25px;
  cursor: pointer;
}
#homework-selfAssess textarea{
       width:90%;
       margin-left: 5%;
       margin-top: 10px;

}

/*提交重置按钮*/

#selfAssessBtn button{
  width:100px;
  margin-bottom:20px;
  margin-left:25%;
}




</style>


