<template>
<div style="margin-top:30px;">
  <div style="width:100% text-align: center;">
			<h1>已结束的课程</h1>
			<router-link :to="'/pages/finished/class_brief/1'" >已结束的课程</router-link>
	</div>
 <div data-spy="scroll" data-target="#navbar-example" data-offset="0" 
   style="overflow-x:visible; position: relative; margin-top:30px">
	<div style="width: 950px; float:left;">
	 <table class="table table-bordered">
		<thead>
			<tr>
				<th>课程名</th>
				<th> </th>
			</tr>
		</thead>
		<tbody>
		<tr  v-for="(course, index) in courses">
			<td>{{ course.name }}</td>
			<td><router-link :to="'/pages/class_brief/'+course.id" class="btn btn-default">进入</router-link></td>
		</tr>
		</tbody>
		</table>
	</div>
	</div>
		
	</div>
</template>

<script type="text/javascript">
import axios from 'axios'
export default {
  name: 'learn_course',
  data () {
    return {
      courses: [],
      fixid: 0,
      creid: 1,
      newClass: [],
      message: '',
      code: '',
      code_error: '',
      code_success: '',
      selected_index: -1
    }
  },
  methods: {
    clearInput: function () {
      this.code_error = ''
      this.code_success = ''
      this.message = ''
    },
    jumpToAsk: function () {
      this.$router.push({path: '/pages/classAsk/' + this.courses[this.selected_index].id})
    },
    attemptJoinCourse: function (index) {
      this.selected_index = index
      console.log(this.selected_index)
      // this.code = this.courses[index].name
      // this.courses.splice(index, 1)
    },
    joinCourse: function () {
      let self = this
      console.log(self.courses)
      console.log(self.selected_index)
      console.log('try######______------>', self.courses[self.selected_index].name)
      axios({
        url: 'https://diningx.cn/pa/public/api/student/join_course',
        method: 'post',
        data: {
          type: 'S2001',
          token: '1f5be77b086bc671b321a66ae4675330',
          course_id: self.courses[self.selected_index].id,
          // class_id: '3',
          // pre_course_score: '84',
          // aim_score: '94',
          aim_text: self.message
        },
        transformRequest: [function (data) {
    // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res)
        console.log(res.data)
      }).catch(function (err) {
        console.log(err)
        /* self.code_error = '该课程密码错误！请重新输入' */
        self.code_success = '下一步'
      })
    },
    getAllCourse: function () {
      let self = this
      console.log('Axios')
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_all_joinable_course',
        method: 'post',
        data: {
          type: 'S2005',
          token: '1f5be77b086bc671b321a66ae4675330'
        },
        transformRequest: [function (data) {
        // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res)
        console.log(res.data.re)
        console.log(res.data.msg[0].name + '-----' + res.data.msg.length)
        self.courses = res.data.msg
        console.log(self.courses)
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.getAllCourse()
  }
}
</script>

<style type="text/css">
th {
	text-align: center;
}
.ax_default {
  font-family:'微软雅黑 Light', '微软雅黑 Regular', '微软雅黑';
  font-weight:290;
  font-style:normal;
  font-size:14px;
  color:#333333;
  text-align:center;
  line-height:normal;
}
.droplist {
  color:#000000;
  text-align:left;
}
.ax_default_visible, .ax_default_unplaced{
    display: none;
    visibility: visible;
}


</style>


