<template>
<div>
  <router-link to="/assess_list" class="link-back"><img :src="iconreback"></router-link>
  <div class="detail" style="margin-top: 20px; width: 900px;">
     <!-- 进度条 -->
      <progressBar greenwidth='0%'></progressBar>
      <!--标题-->
      <br />
      <div id="peerName" style="margin:10px auto;font-size: 18px;font-weight: bold;text-align: center;">{{hwInfo.name}}</div>

      <div id="homework-info">

        <div class="peerRequest">
          <span>作业要求：</span><span id="peerReq">{{hwInfo.requirement}}</span>
        </div>

        <div class="peerRound">
          <span>评估轮次：</span><span id="peerround">{{hwInfo.round}}</span>
        </div>

        <div class="peerExtendFrom">
          <span>源自：</span><span id="peerExtendFro">{{extend_hwInfo}}</span>
        </div>

        <div class="peerDeadline">
          <span>评估截至时间：</span><span id="peerDDL" style="color:red;">{{hwInfo.assessment_ddl}}</span>
        </div>

   </div>

  <h4 class="text-left">我的作业: </h4><textarea name="goal" v-model="content" style="width:600px;height:300px" placeholder=""></textarea>
  
   <button type="button" class="btn btn-default" @click="saveHomework()">保存</button>
  <router-link :to="'/'+hwInfo.id" class="btn btn-default" @click="submitHomework()">提交</router-link>
</div>
</div>
</template>

<script type="text/javascript">
import axios from 'axios'
import store from '@/store.js'
import iconreback from '@/assets/reback.png'
export default {
  data () {
    return {
      homework_id: this.$route.params.id,
      hwInfo: [],
      content: '',
      iconreback: iconreback
    }
  },
  methods: {
    saveHomework: function () {
      store.save('contentElement', this.content)
      alert('保存成功')
    },
    submitHomework: function () {
      let self = this
      console.log('try######______------>', self.items.name)
      axios({
        url: 'https://diningx.cn/pa/public/api/student/submit_homework',
        method: 'post',
        data: {
          type: 'S3001',
          token: '1f5be77b086bc671b321a66ae4675330',
          homework_id: self.homework_id,
          content: self.content
        },
        transformRequest: [function (data) {
        // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            if (Object.prototype.toString.apply(data[it]) === '[object Array]') {
              for (let item in data[it]) {
                ret += encodeURIComponent(it) + '[]=' + encodeURIComponent(data[it][item]) + '&'
              }
            } else {
              ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
            }
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log(res)
        console.log(res.data)
      }).catch(function (err) {
        console.log(err)
      })
    },
    getData: function () {
      let self = this
      axios({
        url: 'https://diningx.cn/pa/public/api/student/get_homework_info_by_id',
        method: 'post',
        data: {
          type: 'S3008',
          token: '1f5be77b086bc671b321a66ae4675330',
          homework_id: self.homework_id
        },
        transformRequest: [function (data) {
          // Do whatever you want to transform the data
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(function (res) {
        console.log('Get HW')
        console.log(res)
        console.log(self.homework_id)
        self.hwInfo = res.data.msg
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.getData()
  }
}
</script>


<style type="text/css">
.link-back{
  float:right;
  width:150px;
}
.link-back img{
  float: right;
}
a {
  text-decoration: none;
}



</style>


