<template>
<div>
  <div class="link-back">
      <router-link to="/learn_course" class="btn btn-default"><img :src="iconreback"></router-link>
    </div>
	<div>
	<ul class="nav nav-tabs">
  <li><router-link :to="'/pages/learning/class_brief/'+course_id">课程简介</router-link></li>
	<li><router-link :to="'/pages/learning/class_notice/'+course_id">教师公告</router-link></li>
	<li><router-link :to="'/pages/learning/class_homework/'+course_id">课程作业</router-link></li>
	<li class="active"><router-link :to="'/pages/learning/class_resources/'+course_id">课程资源</router-link></li>
	<li><router-link :to="'/pages/learning/class_showouts/'+course_id">优秀作品榜</router-link></li>
	<li><router-link :to="'/pages/learning/class_progress/'+course_id">教学进度表</router-link></li>
	</ul>
  </div>
		<div>
			<table class="table table-bordered">
				<thead>
								<tr>
									<th>课件名称</th>
									<th>文件大小</th>
									<th>操作</th>
								</tr>
				</thead>
				<tbody>

								<tr v-for="text in texts">
									<td>{{text.name}}</td>
									<td>{{text.size}}</td>
									<td><a href="" class="btn btn-default">下载</a></td>
								</tr>
				</tbody>
			</table>
		</div>
  
</div>
</template>
<script type="text/javascript">
import iconreback from '@/assets/reback.png'
export default {
  data () {
    return {
      iconreback: iconreback
    }
  }
}
</script>
<style type="text/css">
a {
  text-decoration: none;
}
.link-back{
  float:right;
}


</style>


