<template>
<div>

  <div class="navi">
    <ul class="nav nav-pills nav-stacked">
      <li>
        <router-link to="/pages/index"><img :src="homenol"></router-link>
      </li>
      <li @click="courseShow">
        <router-link to="/add_course" style="font-weight: bold;">课程管理<b class="caret"></b></router-link>
      </li>
      <li v-if="couShow" class="courseManage-sub">
        <router-link to="/add_course">添加课程</router-link>
      </li>
      <li v-if="couShow" class="courseManage-sub">
        <router-link to="/learn_course">学习课程</router-link>
      </li>
      <li v-if="couShow" class="courseManage-sub">
        <router-link to="/finished_course">归档课程</router-link>
      </li>
      <li>
        <router-link to="/assess_list" style="margin-left: -8px;font-weight: bold;">同伴评估</router-link>
      </li>
      <li @click="discussShow">
        <router-link to='' style="font-weight: bold;">讨论交流<b class="caret"></b></router-link> 
      </li>
      <li v-if="disShow" class="discussComm-sub">
        <router-link to="/teacher_notice">社区交流</router-link>
      </li>
      <li v-if="disShow" class="discussComm-sub">
        <router-link to=''>小组消息</router-link>
      </li>
      <li>
        <router-link to='' style="margin-left: -8px;font-weight: bold;">成长报告</router-link>
      </li>
    </ul>
    
  
  </div>
</div>
 
</template>

<script>
import homenol from '@/assets/home-big-blue.png'
export default {
  data () {
    return {
      homenol: homenol,
      couShow: false,
      disShow: false
    }
  },
  methods: {
    courseShow: function () {
      if (this.couShow === true) {
        this.couShow = false
      } else {
        this.couShow = true
      }
    },
    discussShow: function () {
      if (this.disShow === true) {
        this.disShow = false
      } else {
        this.disShow = true
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style type="text/css">
.navi {
  background-color: #FAFAFA;
  border: solid #E3E3E3 1px;
  padding-bottom: 100px;
  float: left;
}
ul {
  width: 100%;
}
ul li a {
  text-align: center;
}
img {
  width:20%;
  height:18%;
  padding-top:2%;
}
.courseManage-sub,.discussComm-sub {
  display: none;
}
.courseManage-sub a,.discussComm-sub a {
  margin-left: -8px;
  color: #6495ED ;
}


</style>


